<template>
    <div>
        <h1 class="divided">伟业</h1>
        <go-back />
        <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div class="cards">
                <div v-for="(v,k) in result" :key="k" class="card-item">
                    <rotate3DCard trigger="hover" direction="row">
                    <!-- 正面 -->
                    <div slot="cz">
                        <div class="item-icon">
                            <div class="overlay"  
                                    :style="`background:url(${cover}) 0px 0px`">
                            </div>
                            <div class="item"
                            :style="`background:url(${target}) -${getIcon(v._Emblem).x}px -${getIcon(v._Emblem).y}px; width:50px; height:50px;`"></div>
                            </div>
                    </div>
                    <!-- 反面 -->
                    <div class="back-card" slot="cf">
                        <div class="remark">
                            <div>名称： <span class="name">{{v.name}}</span></div>
                            <div>达成条件：<span class="target">{{v.subname}}</span></div>
                            {{v.historyname}}
                        </div>
                        <div>奖励</div>
                        <div class="reward">
                            <div v-for="(vv,kk) in v.mailItem" :key="kk">
                                <item-icon-tooltip
                                    :itemId="vv.itemId" 
                                    :itemStub="vv.item"
                                    :count="vv.count"
                                    :noLink="noLink">
                                </item-icon-tooltip> 
                            </div>
                        </div>

                        <div v-if="v.relateItem.length>0">
                            <div>相关物品</div>
                            <div class="reward">
                                <div v-for="(vv,kk) in v.relateItem" :key="kk">
                                    <item-icon-tooltip
                                        :itemId="vv.id" 
                                        :itemStub="vv"
                                        :count="1"
                                        :noLink="noLink">
                                    </item-icon-tooltip> 
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    </rotate3DCard>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import rotate3DCard from "@/components/util/rotate3DCard"
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import { ApiHttpClient } from "@/globals";
export default {
    components:{
        rotate3DCard,
        GoBack,
        ItemIconTooltip
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            cover:`${ApiHttpClient.defaults.resourceURL}/dds/uit_mission_re.png`,
            target:`${ApiHttpClient.defaults.resourceURL}/dds/missiontitleicon01.png`,
        }
    },
    created(){
        this.fetchData();
    },
    methods:{
        fetchData(){
            if(GameInfo.FeatCache){
                this.result = GameInfo.FeatCache;
                this.loading = false;
            }else{
            GameInfo.Feat(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        getIcon(pageIdx){
            let row = Math.floor(pageIdx / 10);
            let column = pageIdx % 10;
            const UNIT_SIZE = 50;

            let ret = {
                x: Math.max(UNIT_SIZE * column, 0),
                y: UNIT_SIZE * row,
            };
            return ret;
        },
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.cards{
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
    .card-item{
        margin-bottom:2rem;
        flex: 0 0 25%;

        .item-icon {
            width: 100px;
            height: 148px;
            position: relative;
            display: inline-block;

            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }

            .item {
                position: absolute;
                top: 40px;
                .left(24px);
                .right(0);
                bottom: 0;
            }
        }
    }
    .back-card{
        .remark{
            .name{
                color: @dv-c-foreground;
            }

            .target{
                color: @dv-c-foreground;
            }
        }
    }
}


</style>